<template>
    <div class="container">
        <v-back title="学院综合" />
        <div class="from">
            <md-field>
                <md-field-item title="选择日期" :content="selectorValue1" @click="showSelector1" arrow solid />
                <md-field-item title="选择学院" :content="selectorValue2" @click="showSelector2" arrow solid />
            </md-field>
            <md-date-picker
                ref="datePicker"
                v-model="isSelectorShow1"
                type="custom"
                title="选择日期"
                large-radius
                :custom-types="['yyyy', 'MM','dd']"
                :default-date="currentDate"
                @confirm="onSelectorConfirm1" ></md-date-picker>
            <md-selector v-model="isSelectorShow2" :data="data2" @confirm="onSelectorConfirm2" min-height="320px" max-height="320px" title="选择学院" okText="确认" large-radius></md-selector>
        </div>


        <div class="list flex space-between">
            <div class="list_one bgcolor-1" @click="toList(0, num[0].num)">
                <div>
                    <md-icon name="home" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[0].num || 0}}</p>
                        <div class="title">学生总数量</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-2" @click="toList(1, num[1].num)">
                <div>
                    <md-icon name="share" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[1].num || 0}}</p>
                        <div class="title">全部通行记录</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-3" @click="toList(2, num[2].num)">
                <div>
                    <md-icon name="back" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[2].num || 0}}</p>
                        <div class="title">24-72小时未归寝人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-4" @click="toList(3, num[3].num)">
                <div>
                    <md-icon name="calendar" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[3].num || 0}}</p>
                        <div class="title">72小时以上未归寝人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-5" @click="toList(4, num[4].num)">
                <div>
                    <md-icon name="switch" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[4].num || 0}}</p>
                        <div class="title">24小时以上未出寝人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-6" @click="toList(5, num[5].num)">
                <div>
                    <md-icon name="clock" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[5].num || 0}}</p>
                        <div class="title">当日未归人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-7" @click="toList(6, num[6].num)">
                <div>
                    <md-icon name="authentication" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[6].num || 0}}</p>
                        <div class="title">闭寝后晚归人员</div>
                    </div>
                </div>
            </div>
            <div class="list_one bgcolor-8" @click="toList(7, num[7].num)">
                <div>
                    <md-icon name="motor-vehicle" size="lg"></md-icon>
                    <div class="title_box">
                        <p>{{num[7].num || 0}}</p>
                        <div class="title">闭寝后早出人员</div>
                    </div>
                </div>
            </div>
			<div class="list_one bgcolor-9">
				<div>
				    <md-icon name="card-bag" size="lg"></md-icon>
				    <div class="title_box">
				        <p>{{num[8].num || 0}}</p>
				        <div class="title">请假实习</div>
				    </div>
				</div>
			</div>
        </div>
    </div>
</template>

<script>
    import dayjs from 'dayjs'
	export default {
		name: 'report2_index',
		data() {
			return { 
                selectorValue1: '',
                selectorValue2: '',
                isSelectorShow1: false,
                isSelectorShow2: false,
                currentDate: new Date(),
                value1: '',
                value2: '',
                data2: [],
                num: [{type: 0, num: 0},{type: 1, num: 0},{type: 2, num: 0},{type: 3, num: 0},{type: 4, num: 0},{type: 5, num: 0},{type: 6, num: 0},{type: 7, num: 0},{type: 8, num: 0}]
            }
        },
        methods: {
            //获取学院
            getDepartment() {
                this.$getL('/college/department', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id
                }, (res)=>{
                    this.data2 = []
                    res.data.forEach((item) => {
                        this.data2.push({
                            value: item.orgId,
                            text: item.orgName
                        })
                    })
                    if (this.value2 == '' && res.data.length > 0) {
                        this.value2 = res.data[0].orgId
                        this.selectorValue2 = res.data[0].orgName
                    }
                    setTimeout(() => {
                    	this.getData()
                    }, 200)
                })
            },
            showSelector1() {
                this.isSelectorShow1 = true
            },
            showSelector2() {
                this.isSelectorShow2 = true
            },
            onSelectorConfirm1() {
                this.value1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
                this.selectorValue1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
                this.getData()
            },
             onSelectorConfirm2({text, value}) {
                this.value2 = value
                this.selectorValue2 = text
                this.getData()
            },
            toList(type, total) {
                this.$router.push('/report2/list?type=' + type + '&time=' + this.value1 + '&collegeId=' + this.value2 + '&collegeName=' + this.selectorValue2 + '&total=' + total)
            },
            getData() {
                this.$getL('/report/list_new', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    time: this.value1,
                    orgId: this.value2
                }, (res)=>{
                    localStorage.setItem('report2Storage', JSON.stringify({
                        value1: this.value1,
                        value2: this.value2,
                        selectorValue1: this.selectorValue1,
                        selectorValue2: this.selectorValue2
                    }))
                    this.$toast.hide()
                    this.num = res.data
                })
                this.$toast.hide()
            }
        },
        mounted() {
            this.selectorValue1 = dayjs(new Date()).format('YYYY-MM-DD')
            this.value1 = dayjs(new Date()).format('YYYY-MM-DD')

            if (localStorage.getItem('report2Storage')) {
                let reportStorage = JSON.parse(localStorage.getItem('report2Storage'))
                this.selectorValue1 = reportStorage.selectorValue1
                this.selectorValue2 = reportStorage.selectorValue2
                this.value1 = reportStorage.value1
                this.value2 = reportStorage.value2
            }
            this.getDepartment()
        }
    }
</script>

<style scoped>
    .container {
        height: 100vh;
        overflow-y: auto;
    }
    .list {
        font-size: 4vw ;
        flex-wrap: wrap;
        padding: 2vw;
    }
    .list_one {
    	box-sizing: border-box;
    	width: calc(33% - 1vw);
    	color: #fff;
    	padding: 2vw;
    	margin-bottom: 2vw;
    	height: 30vw;
    	border-radius: 0%;
    	box-shadow: 2px 2px 5px rgba(0,0,0,.2);
    	display: flex;
    	align-items: center;
    	justify-content: center;
    	position: relative;
    }
    .list_one > div {
    	box-sizing: border-box;
    	background: rgba(250,250,250,0.2);
    	width: 100%;
    	height: 100%;
    	padding: 2vw 1vw;
    	text-align: center;
    }
    .list_one .md-icon {
    	font-size: 24vw;
    	opacity: .15;
    	position: absolute;
    	left: 3.5vw;
		top: 3vw;
		z-index: 0;
    }
    .list_one p {
        font-size: 9vw;
        text-align: center;
    }
    .title {
        text-align: center;
        font-size: 3.5vw;
    }
	.title_box {
		position: relative;
		z-index: 1;
	}
</style>